.header_languages {
	&:not(:last-child) {
		margin-right: 2rem;
	}

	@include bp($point_4 - 1) {
		&.header_languages--tablet_mod {
			display: none;
		}
	}
}

.header_languages__list {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		margin: 0 max(-1.4vw, -2rem) -2rem 0;
	}

	@include bp($point_3, $point_4) {
		margin: 0 -2.2rem -2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1.2rem -2rem 0;
	}
}

.header_languages__item  {
	@include bp($point_2, min) {
		padding: 0 min(1.4vw, 2rem) 2rem 0;
	}

	@include bp($point_3, $point_4) {
		padding: 0 2.2rem 2rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.2rem 2rem 0;
	}
}

.header_languages__link {
	@extend %transition_color;
	@extend %active_border;

	display: block;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $green_2;

	&:hover,
	&:focus{
		text-decoration: none;
		color: $orange;
	}

	&.header_languages__link--active_state {
		color: $orange;

		&::before{
			opacity: 1;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		padding: 0 2px;
	}
}