%form_el_checkbox {
	position: relative;

	padding-left: 2.5rem;

	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.4;
	color: $green;
	cursor: pointer;

	&:before,
	&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 1.5rem;
		height: 1.5rem;
		border-radius: 50%;
		border: 1px solid $gray_3;

		cursor: pointer;
		content: "";
	}

	&:before {
		.form_checkbox__element:checked + & {
			opacity: 0;
		}
	}

	&::after {
		background-color: $orange;

		transition: opacity 0.2s;

		.form_checkbox__element:checked + & {
			opacity: 1;
		}
	}

