.hero {
	min-height: calc(var(--full-screen) - var(--height-header));
}

.hero__slider_wrap {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.hero__slider {
	height: 100%;
}

.hero__bg {
	z-index: -2;
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	&:after {
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.4);

		content: "";
	}
}

.hero__pic {
	display: block;
	width: 100%;
	height: 100%;
}

.hero__pic_img {
	@extend %cover_img;

	&.hero__pic_img--pos_mod {
		@include bp($point_1, min) {
			object-position: right;
		}

		@include bp($point_1 - 1) {
			object-position: left;
		}
	}
}

//==============First slide==============
.hero__content_wrap {
	height: 100%;

	@include flex_block(column, wrap, center, center, center);

	@include bp($point_2, min) {
		padding-bottom: 9rem;
	}

	@include bp($point_3) {
		padding: 6rem var(--gl-indent);
	}
}

.hero__content {
	&:not(.hero__col_text &) {
		text-align: center;
		color: $white;
	}

	.hero__col_text & {
		max-width: 36rem;
	}
}

//==============First slide###===========

.hero__subtitle {
	margin-bottom: 0.6rem;

	font-family: $font_2;
	line-height: 1;
	font-size: 2rem;
	text-transform: uppercase;
}

.hero__title {
	@extend %h2;
	line-height: 1.211;

	@include bp($point_4, min) {
		margin-bottom: 2.8rem;
		font-size: 4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 4rem;
		font-size: 3rem;
	}
}

.hero__link {
	@extend %transition_opacity;

	display: block;
	font-size: 1.6rem;
	font-weight: 400;
	letter-spacing: 0.045em;
	text-transform: uppercase;

	&:not(.hero__col_text &) {
		color: $gray_2;
	}

	.hero__col_text & {
		color: $orange;
	}

	&:focus,
	&:hover {
		text-decoration: none;
		opacity: 0.7;
	}
}

.hero__row {
	height: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center);

		.hero__slider_item:nth-child(4) & {
			background: #efefef;
		}
	}
}

.hero__col_text {
	padding: 2rem var(--gl-indent) 8.8rem;

	@include bp($point_2, min) {
		&:not(.hero__row--wide_mod &) {
			width: 35%;
		}
		.hero__row--wide_mod & {
			width: 40%;
		}
	}

	@include bp($point_3) {
		text-align: center;
		height: 100%;
		@include flex_block(column, wrap, center, center, center);
	}
}

.hero__col_picture {
	height: 100%;

	@include bp($point_2, min) {
		&:not(.hero__row--wide_mod &) {
			width: 65%;
		}
		.hero__row--wide_mod & {
			width: 60%;
		}
	}

	@include bp($point_3) {
		z-index: -2;
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		&:after {
			position: absolute;
			top: 0;
			left: 0;

			display: block;
			width: 100%;
			height: 100%;
			background: rgba(233, 233, 233, 0.7);

			content: "";
		}
	}
}

.hero__dots_wrap {
	position: absolute;
	z-index: 10;
	left: 50%;
	transform: translateX(-50%);
	bottom: 2.5rem;
}

.hero__dots {
	counter-reset: dots;
	@include flex_block(row, nowrap, flex-start, center);
}

.swiper-pagination-bullet {
	.hero & {
		position: relative;
		display: block;
		margin: 0 2rem !important;
		width: 3rem;
		height: 3rem;
		padding: 4px;
		background: $gray_4;
		opacity: 1;

		&:after {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			font-size: 1.4rem;
			// line-height: 1;
			line-height: 0.8;
			color: $green_2;
			opacity: 0.5;
			counter-increment: dots;
			content: counter(dots, decimal-leading-zero);
		}
	}
}

.swiper-pagination-bullet-active {
	.hero & {
		&:after {
			opacity: 1;
		}
	}
}
