%h1 {
	font-family: $font_2;

	font-weight: 500;
	line-height: 1.025;
	text-transform: uppercase;

	@include bp($point_5, min) {
		font-size: 9rem;
	}

	@include bp($point_5 - 1) {
		font-size: 5rem;
	}
}

%h2 {
	font-family: $font_2;
	font-weight: 500;
	line-height: 1.2;
	text-transform: uppercase;

	@include bp($point_5, min) {
		font-size: 4rem;
	}

	@include bp($point_5 - 1) {
		font-size: 2rem;
	}
}

%h3 {
	font-family: $font_2;
	font-weight: 500;
	letter-spacing: 0.204em;
	text-transform: uppercase;

	@include bp($point_4 + 1, min) {
		font-size: 3rem;
	}

	@include bp($point_4) {
		font-size: 2rem;
	}
}

%h4 {
	font-family: $font_2;
	font-weight: 500;
	line-height: 1.2;
	text-transform: uppercase;

	@include bp($point_5, min) {
		font-size: 2rem;
	}

	@include bp($point_5 - 1) {
		font-size: 1.4rem;
	}
}

// offsets

%title_offset {
	margin-bottom: 2rem;
}

%text {
	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.35;
}

%text_2 {
	font-size: 1.4rem;
	font-weight: 300;
}

%title_card {
	font-weight: 500;
	line-height: 1.38;
	text-transform: uppercase;
	color: $green_2;
	text-align: center;

	@include bp($point_4 + 1, min) {
		font-size: 1.8rem;
	}

	@include bp($point_4) {
		font-size: 1.4rem;
	}
}

%text_card {
	font-weight: 300;
	line-height: 1.5;
	text-transform: capitalize;

	@include bp($point_4 + 1, min) {
		font-size: 1.6rem;
	}

	@include bp($point_4) {
		font-size: 1.4rem;
	}
}

%price_card {
	line-height: 1.42;
	letter-spacing: 0.045em;
	text-transform: uppercase;
	font-family: $font_3;

	@include bp($point_4 + 1, min) {
		font-size: 1.4rem;
	}

	@include bp($point_4) {
		font-size: 1.3rem;
	}
}

%title_account {
	font-family: $font_1;
	font-weight: 500;
	
	text-align: center;
	letter-spacing: 0.03em;
	text-transform: uppercase;

	color: $green_2;
	
	@include bp($point_2, min) {
		font-size: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
	}
}