@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color extends
%blue_btn_1 {
	font-weight: 700;
	color: $gray_2;
	text-transform: uppercase;
	background-color: $color_link_base;

	transition: background-color .3s ease, opacity .3s ease;
	will-change: background-color, opacity;

	@include st_btn();

	&:hover {
		background-color: darken($color_link_base, 10%);
	}
}

%dark_btn_1 {
	color: $gray_2;
	text-transform: uppercase;
	background-color: $green_2;
	transition: background-color .3s ease;

	&:hover, &:focus {
		background-color: lighten($green_2, 7%);
	}
}

%transparent_btn_1 {
	color: $green_2;
	background-color: $white;
	text-transform: uppercase;

	border: 1px solid $green_2;
	transition: background-color .3s ease;

	&:hover, &:focus {
		background-color: darken($white, 7%);
	}
}

//size extends
%btn_size_1 {
	height: 5rem;
	padding: 0 1.2rem;

	line-height: 5rem;
	font-size: 1.6rem;
}
