.header_menu {
	flex: 1 1 auto;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.4rem;
		}
	}

	@include flex_block(row, wrap, center, flex-start);
}

.header_menu__body {
	@include bp($point_2, min) {
		width: 100%;
	}

	&:not(.header_menu__body--active_state) {
		display: none;
	}
}

.header_menu__list {
	@include bp($point_2, min) {
		margin:  0 -5vw -2rem 0;
		padding: 0 5.9vw 0 10.1vw;

		@include flex_block(row, wrap, flex-start);
	}
}

.header_menu__item {
	@include bp($point_2, min) {
		width: 20%;
		padding: 0 5vw 2rem 0;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.header_menu__head {
	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);
	}
}

.header_menu__label {
	@extend %transition_color;

	display: block;

	font-family: $font_1;
	font-weight: 700;
	font-size: 1.4rem;
	color: $green_2;
	text-transform: uppercase;

	&:hover,
	&:focus{
		text-decoration: none;
		color: $orange
	}
	
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}
		@include bp($point_3) {
			margin-right: 1.4rem;
		}
	}
}

.header_menu__icon {
	position: relative;

	flex-shrink: 0;
	width: 1rem;
	height: 1rem;

	cursor: pointer;

	&::before,&::after{
		position: absolute;
		top: 50%;
		left: 0;

		width: 100%;
		height: 1px;

		background-color: $green_2;
		content:'';

		.header_menu__item--active_state & {
			background-color: $orange;
		}
	}

	&::before{
		transition: background .3s ease;
		will-change: transform, background;
	}

	&::after{
		transform: rotate(90deg);
		transition: background .3s ease, transform .3s ease;
		will-change: transform, background;

		.header_menu__item--active_state & {
			transform: translateY(-50%) rotate(0deg);
		}
	}
	
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}
}

.header_menu__sublist {
	@include bp($point_3) {
		width: 100%;

		max-height: 0;
		opacity: 0;
		padding-top: 1.5rem;
	
		transition: max-height .25s ease-out, opacity .25s ease-out;
		will-change: max-height, opacity;
		overflow: hidden;
		pointer-events: none;
		
		&:not(.header_menu__item--active_state & ) {
			padding-bottom: 2.2rem;
		}
	
		.header_menu__item--active_state & {
			opacity: 1;
			pointer-events: auto;
		}
	}
}

.header_menu__subitem {
	@include bp($point_3) {
		text-align: center;
	}
}

.header_menu__link {
	position: relative;

	display: block;
	font-weight: 300;
	font-size: 1.4rem;
	color: $green_2;

	transition: color .3s ease, transform .3s ease;
	will-change: color, transform;

	&::before{
		@extend %transition_opacity;

		position: absolute;
		top: 50%;
		left: 0;
		z-index: 1;
		transform: translate(-1.2rem, -2px);

		width: 5px;
		height: 5px;
		border-radius: 50%;

		opacity: 0;
		background-color: $orange;
		content:'';
	}

	&:hover,
	&:focus{
		color: $orange;
		transform: translateX(1.2rem);
		text-decoration: none;

		&::before{
			opacity: 1;
		}
	}

	@include bp($point_2, min) {
		line-height: 1.7;
	}

	@include bp($point_3) {
		line-height: 2;
	}
}