.empty_cart_section {
	min-height: 55.1rem;
	padding-top: 4.5rem;

	@include bp($point_4) {
		border-bottom: 1px solid $gray_2;
	}
}

.empty_cart_section__wrap {
	@extend %global_flex_block_column_nowrap_flex-start_center;

	max-width: 39rem;
	margin: 0 auto;
}

.empty_cart_section__title {
	font-weight: 500;
	font-size: 2.5rem;
	line-height: 1;
	text-align: center;
	letter-spacing: 0.03em;
	text-transform: uppercase;
	color: $green_2;

	&:not(:last-child) {
		margin-bottom: 7.5rem;
	}
}

.empty_cart_section__img_in {
	position: relative;
	width: 29.23%;
	padding-bottom: 22.57%;

	&::after {
		content: "";
		position: absolute;
		bottom: -4px;
		left: 50%;

		width: 346.6%;
		height: 4px;

		background: #e8e8e8;
		border-radius: 50%;
		filter: blur(2.5px);

		transform: translateX(-50%);
	}

	&:not(:last-child) {
		margin-bottom: 4.5rem;
	}
}

.empty_cart_section__img {
	@extend %stretch;
	@extend %contain_img;
}

.empty_cart_section__text {
	@extend %text;

	text-align: center;
	letter-spacing: 0.03em;
	color: $green;

	&:not(:last-child) {
		margin-bottom: 1.9rem;
	}
}

.empty_cart_section__btn {
	@extend %btn_size_1;
	@extend %blue_btn_1;

	min-width: 25rem;
}
