// actions
.header_actions {
	margin-left: auto;
}

.header_actions__list  {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		margin: 0 max(-1.16vw, -1.7rem) -1.7rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.7rem -1.7rem 0;
	}
}

.header_actions__item {
	@include bp($point_2, min) {
		padding: 0 min(1.16vw, 1.7rem) 1.7rem 0;
	}

	@include bp($point_3) {
		padding: 0 1.7rem 1.7rem 0;
	}
}

.header_actions__link {
	@extend %transition_color;
	
	display: block;
	color: $green_2;

	&:hover,
	&:focus{
		color: $orange;
	}

	@include bp($point_2, min) {
		width: 2rem;
		height: 1.8rem;
	}

	@include bp($point_3) {
		width: 1.8rem;
		height: 1.7rem;
	}
}