%active_border {
	position: relative;

	&::before{
		@extend %transition_opacity;

		position: absolute;
		left: 50%;
		transform: translate(-50%, 50%);

		width: 100%;
		height: 2px;
		
		opacity: 0;
		background-color: $orange;
		content:'';

		@include bp($point_2, min) {
			bottom: -1.9rem;
		}

		@include bp($point_3, $point_4) {
			bottom: -1.2rem;
		}

		@include bp($point_4 - 1) {
			bottom: -1.3rem;
		}
	}
}
// ^ this extend need for header_languages, header_list components

// components
@import './header_components/header_list';
@import './header_components/header_languages';
@import './header_components/header_actions';
@import './header_components/header_bottom';
@import './header_components/header_menu';

//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;

	font-size: 1rem;

	&:not(.body--open_menu_state &) {
		height: var(--height-header);
	}

	.body--open_menu_state & {
		@include bp($point_2, min) {
			height: min-content;
		}

		@include bp($point_3) {
			height: 100%;
		}
	}

	&::before {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 5;

		width: 100%;
		height: 100%;
		max-height: var(--height-header);

		content:'';
		background-color: $white;
		border-bottom: 1px solid $gray_2;
	}

	@include z-index(header);

	@include bp($point_2, min) {
		padding: 1.9rem var(--gl-indent);
	}

	@include bp($point_3, $point_4) {
		padding: 1.6rem var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		padding: 1.8rem var(--gl-indent);
	}
}

//------------------------------------------------------------layout###

.header_in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;

}

.header__row {
	position: relative;
	z-index: 6;

	margin:  0 -2.6rem -2rem 0;

	@include flex_block(row, wrap, flex-start, center, center);
}

.header__column {
	padding: 0 2.6rem 2rem 0;

	@include bp($point_1, min) {
		&:nth-child(4n+1){
			width: 30.26%;
		}
		
		&:nth-child(4n+2){
			width: 47.3%;
		}
	
		&:nth-child(4n+3){
			width: 22.44%;
		}
	}

	@include bp($point_2, min) {
		&.header__column--mob_mod {
			display: none;
		}
	}

	@include bp($point_1 - 1, $point_2) {
		&:nth-child(3n+1){
			width: 17%;
		}
		
		&:nth-child(3n+2){
			width: 55%;
		}
	
		&:nth-child(3n+3){
			width: 28%;
		}
	}

	@include bp($point_3) {
		&:first-child{
			flex: 1 1 auto;
		}

		&:nth-child(2),
		&:nth-child(3) .header_languages {
			display: none;
		}
	}
}

.header__wrap {
	@include flex_block(row, wrap, flex-start, center, center);
}

// logo
.header_logo {
	display: block;

	@include bp($point_2, min) {
		width: 12.9rem;
		height: 1.6rem;
	}

	@include bp($point_3) {
		width: 11.6rem;
		height: 1.44rem;
	}
}

.header_logo__in {
	@extend %contain_img;
}

// menu trigger
.header__menu_trigger {
	position: relative;
	z-index: 5;

	width: 2rem;
	height: 1.7rem;

	overflow: hidden;
	cursor: pointer;

	&::before,&::after{
		@extend %transition_transform;

		content:'';
		position: absolute;
		top: 50%;
		right: 0;

		width: 100%;
		height: 1px;

		background-color: $green_2;
	}

	&::before{
		transform: translateY(-.4rem);
	}

	&::after{
		transform: translate(50%, .4rem);
	}

	.body--open_menu_state & {
		&::before{
			transform: rotate(-45deg);
		}

		&::after{
			transform: rotate(45deg);
		}
	}
}

// burger menu
.header__body {
	@extend %transition_transform;
	@extend %stretch;

	z-index: 4;

	display: flex;
	flex-direction: column;
	width: 100%;
	max-height: var(--full-screen);

	overflow-x: hidden;
	overflow-y: auto;
	background: $white;

	&:not(.body--open_menu_state &) {
		transform: translateY(-100%);
	}

	@include bp($point_2, min) {
		height: max-content;
		padding: calc(var(--height-header) + 4.5rem) var(--gl-indent) 2.4rem;
	}

	@include bp($point_3) {
		// height: var(--full-screen);
		height: 100%;
	}

	@include bp($point_3, $point_4) {
		padding: calc(var(--height-header) + max(13.2vh, 4rem)) var(--gl-indent) 4rem;
	}

	@include bp($point_4 - 1) {
		padding: calc(var(--height-header) + 5rem) var(--gl-indent) 1.8rem;
	}
}

.header__content {
	width: 100%;

	@include bp($point_2, min) {
		position: relative;

		max-width: $page_width;
		margin: 0 auto;
	}

	@include bp($point_3) {
		flex: 1 1 auto;
		display: flex;
		flex-direction: column;
	}
}

.header__decor {
	position: absolute;
	top: -4.5rem;
	left: 0;

	width: 1px;
	height: calc(100% + 4.5rem);
	background-color: $gray_2;

	@include bp($point_1, min) {
		&.header__decor--mod_1 {
			margin-left: 24.2%;
		}
	
		&.header__decor--mod_2 {
			margin-left: 41.4%;
		}
	
		&.header__decor--mod_3 {
			margin-left: 58.5%;
		}
	
		&.header__decor--mod_4 {
			margin-left: 75.7%;
		}
	}

	@include bp($point_1 - 1) {
		&.header__decor--mod_1 {
			margin-left: 25%;
		}
	
		&.header__decor--mod_2 {
			margin-left: 43%;
		}
	
		&.header__decor--mod_3 {
			margin-left: 60%;
		}
	
		&.header__decor--mod_4 {
			margin-left: 79%;
		}
	}

	@include bp($point_3) {
		display: none;
	}
}

.header__top {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: max(15.3vh, 6.4rem);
		}
	}
	
	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 6.4rem;
		}
	}
}

.header__items {
	margin-bottom: -2rem;

	@include flex_block(row, wrap, center, center, center);
}

.header__item {
	padding-bottom: 2rem;
}
