.shopping_cart_section {
	padding-top: 4.5rem;
	padding-bottom: 0;

	@include bp($point_3, $point_4) {
		border-bottom: 1px solid $gray_2;
	}

	@include bp($point_4 - 1) {
		padding: 3rem 2rem;
	}
}

.shopping_cart_section__in {
	max-width: 84rem;
}

.shopping_cart_section__title {
	position: relative;

	padding-bottom: 3rem;

	font-weight: 500;
	font-size: 2.5rem;
	line-height: 1;

	text-align: center;
	letter-spacing: 0.03em;
	text-transform: uppercase;

	color: $green_2;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 50%;

		width: 100vw;
		height: 1px;

		transform: translateX(-50%);
		background-color: $gray_2;
	}
}

.shopping_cart_section__wrap {
	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}
}

.shopping_cart_section__column {
	position: relative;

	@include bp($point_4, min) {
		flex-shrink: 0;
		width: 50%;

		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;

			height: 100%;
			width: 1px;

			background-color: $gray_2;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.shopping_cart_section_form {
	.form_radio:not(:last-child) {
		margin-bottom: 1.3rem;
	}

	@include bp($point_4, min) {
		padding: 2.6rem 2rem 2.5rem 3rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.6rem 0 2.5rem;
	}
}

.shopping_cart_section_form__list {
	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.shopping_cart_section_form__item {
	max-width: 25rem;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.shopping_cart_section_form__title {
	font-weight: 700;
	font-size: 1.4rem;
	line-height: 1.7;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	color: $green;

	&:nth-child(2n - 1) {
		&:not(:last-child) {
			margin-bottom: 0.9rem;
		}
	}

	&:nth-child(2n - 2) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.shopping_cart_section_form__delivery {
	font-weight: 400;
	font-size: 1.5rem;
	letter-spacing: -0.01em;
	line-height: 1.4;

	color: $green;

	&:not(:last-child) {
		margin-bottom: 1.1rem;
	}
}

.shopping_cart_section_form__links {
	@extend %global_flex_block_column_nowrap_flex-start;
	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}

.shopping_cart_section_form__link {
	@extend %text;
	@extend %transition_opacity;

	color: $orange;
	letter-spacing: 0.04em;

	&:focus,
	&:hover {
		text-decoration: none;
		opacity: 0.6;
	}

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.shopping_cart_section_form_button {
	@extend %btn_size_1;
	@extend %blue_btn_1;

	min-width: 25rem;

	&.shopping_cart_section_form_button--disabled_mod {
		opacity: 0.3;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		margin-left: -3px;
	}
}

.shopping_cart_section_form__checkbox {
	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}

.shopping_cart_section__list {
	@include bp($point_4, min) {
		padding-top: 1.5rem;
	}
}

.shopping_cart_section_item {
	position: relative;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;

		height: 1px;

		background-color: $gray_2;

		@include bp($point_4, min) {
			width: 100vw;
		}

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}

	@include bp($point_4, min) {
		padding: 4.6rem 0 4.5rem 4rem;
	}

	@include bp($point_4 - 1) {
		padding: 3rem 0;
		&:not(:last-child) {
			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;

				height: 1px;
				width: 100%;

				background-color: $gray_2;
			}
		}
	}
}

.shopping_cart_section_item__btn_close {
	@extend %transition_color;

	display: block;
	width: 2rem;
	height: 2rem;
	margin-left: auto;

	color: $green_2;
	cursor: pointer;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	&:focus,
	&:hover {
		color: $orange;
	}
}

.shopping_cart_section_item__card {
	@include flex_block(row, wrap, flex-start, stretch);
}

.shopping_cart_section_item__img_in {
	position: relative;

	width: 14rem;
	height: 17rem;

	&:not(:last-child) {
		margin-right: 2.5rem;
	}
}

.shopping_cart_section_item__img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.shopping_cart_section_item__content {
	@extend %global_flex_block_column_nowrap_flex-start;

	padding-top: 1.6rem;
}

.shopping_cart_section_item__title {
	@extend %title_card;

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray_3;
	}
}

.shopping_cart_section_item__category {
	@extend %text_card;

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray_3;
	}

	&:not(:last-child) {
		margin-bottom: 0.7rem;
	}
}

.shopping_cart_section_item__prices_in {
	@extend %global_flex_block_row_wrap_center_center;

	padding-left: 3px;
}

.shopping_cart_section_item__price {
	@extend %price_card;

	&.shopping_cart_section_item__price--normal_mod {
		font-weight: 700;
		color: $green_2;
	}

	&.shopping_cart_section_item__price--old_mod {
		position: relative;

		font-weight: 500;
		color: $gray_4;

		&:not(:last-child) {
			margin-right: 0.55rem;
		}

		&::after {
			content: "";
			position: absolute;
			top: 0.8rem;
			left: -1px;

			width: 104%;
			height: 2px;

			background-color: $gray_4;
		}
	}

	&.shopping_cart_section_item__price--new_mod {
		font-weight: 700;
		color: $red;
	}
}

.shopping_cart_section__total {
	position: relative;

	font-size: 2rem;
	line-height: 1.35;
	text-transform: capitalize;

	color: $green_2;

	span {
		font-weight: 300;

		&:not(:last-child) {
			margin-right: 1.8rem;
		}
	}

	strong {
		font-weight: 900;
	}

	@include bp($point_4, min) {
		padding: 3.1rem 4rem;
	}

	@include bp($point_4 - 1) {
		padding: 3.1rem 0;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;

			height: 1px;
			width: 100%;

			background-color: $gray_2;
		}
	}
}
