%form_el_radio {
	position: relative;

	padding-left: 2.5rem;

	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.4;
	letter-spacing: 0.04em;
	color: $green;
	cursor: pointer;

	&:before,
	&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 1.5rem;
		height: 1.5rem;
		border-radius: 50%;
		border: 1px solid $gray_3;

		cursor: pointer;
		content: "";
	}

	&:after {
		@extend %transition_opacity;

		background-color: $orange;
		opacity: 0;

		.form_radio__element:checked + & {
			opacity: 1;
		}
	}
}
