@use "sass:map";

%cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

%contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

%popup {
	@include flex_block(row, wrap, center, center, stretch);
	z-index: -1;
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;

	overflow-y: auto;

	background-color: rgba($black, .75);

	opacity: 0;
	pointer-events: none;
	transition: opacity .3s ease; // значение милисекунд должно совпадать с "const timeout" в initPopup.js


	&.popup--open {
		z-index: map.get($z-index, "popup");
		opacity: 1;
		pointer-events: initial;
	}

	@include bp($point_4, min) {
		padding: 3rem var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		overflow-x: hidden;
	}

	&::-webkit-scrollbar {
		display: none;
	}
	-ms-overflow-style: none;
	scrollbar-width: none;
}

%sidebar_padding {
	@include bp($point_2, min) {
		padding: 0 2.5rem 0 0;
	}

	@include bp($point_3, $point_6) {
		padding: 0 4rem;
	}

	@include bp($point_6 - 1) {
		padding: 0 var(--gl-indent);
	}
}

%filter_button {
	position: relative;

	width: 100%;
	min-height: 4rem;
	padding: 0 3rem 0 1.4rem;

	font-size: 1.4rem;
	font-weight: 300;

	color: $green_2;
	text-align: start;
	border: 1px solid $gray_2;
	cursor: pointer;

	&::after {
		@extend %transition_transform;

		position: absolute;
		top: 50%;
		right: 0;

		width: 0;
		height: 0;

		content: "";
		border-style: solid;
		border-width: 4.5px 3px 0 3px;
		border-color: $green_2 transparent transparent transparent;

		transform: translate(-1.5rem, -50%);

		.form_element--active-mod & {
			transform: translate(-1.5rem, -50%) rotateX(180deg);
		}
	}
}

%fav_btn {
	position: absolute;

	background-color: $white;
	color: $green_2;

	border-radius: 50%;
	cursor: pointer;
	transition: background-color 0.3s ease;

	@include flex_block(row, nowrap, center, center);

	&:focus,
	&:hover {
		background-color: darken($white, 7%);
	}

	@include bp($point_6 + 1, min) {
		width: 4.5rem;
		height: 4.5rem;
	}

	@include bp($point_6) {
		width: 3.5rem;
		height: 3.5rem;
	}
}

%banner_bg {
	&::after {
		content: "";
		@extend %stretch;

		z-index: 1;

		background-color: rgba(0, 0, 0, 0.3);
	}
}
