.header_bottom {
	@include bp($point_2, min) {
		position: relative;
		z-index: 2;

		padding-top: 2rem;
		background-color: $white;

		&::before{
			position: absolute;
			top: 0;
			left: calc(50% - 50vw);
			z-index: 1;

			width: 100vw;
			height: 1px;

			background-color: $gray_2;
			content:'';
		}
	}
}

.header_bottom__wrap {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 5.4rem;
		}
	}
}

.header_bottom__row {
	margin-bottom: -4rem;

	@include flex_block(row, wrap, center, center, center);
}

.header_bottom__column {
	padding-bottom: 4rem;
}

.header_bottom__list {
	@include flex_block(row, wrap, center);

	@include bp($point_4, min) {
		margin:  0 -4rem -2rem 0;
	}

	@include bp($point_4 - 1) {
		margin:  0 -1.6rem -1.6rem 0;
	}
}

.header_bottom__item {
	@include bp($point_4, min) {
		padding: 0 4rem 2rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.6rem 1.6rem 0;
	}
}

.header_bottom__link {
	@extend %transition_color;

	font-weight: 300;
	line-height: 1.33;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $green;

	&:hover,
	&:focus{
		color: $orange;
		text-decoration: none;
	}

	@include bp($point_4, min) {
		font-size: 1.2rem;
	}

	@include bp($point_4 - 1) {
		font-size: .8rem;
	}
}