@import "ScssHelpers/_helpers_all.scss";

.wish_list {
	padding: 0 var(--gl-indent);
}

.wish_list_in {
	max-width: 84rem;
}

.wish_list__block {
	width: 100%;
}

.wish_list__list {
	position: relative;

	@include flex_block(row, wrap, flex-start, stretch, flex-start);

	&::before { 
		position: absolute; 
		bottom: -1px;
		left: -50%;
		
		width: 1000rem; 
		height: 1px; 

		background-color: #EAE9E8;
		
		content: '';
	}
}

.wish_list__item {
	position: relative;

	&::before { 
		position: absolute; 
		top: 0; 
		
		width: 1000rem; 
		height: 1px; 

		background-color: #EAE9E8;
		
		content: '';

		@include bp($point_4, min) {
			width: 1000rem; 
		}
		@include bp($point_4 - 1) {
			width: calc(100% + var(--gl-indent) * 2);
		}
	}

	@include bp($point_4, min) {
		width: 50%;

		&:nth-child(2n + 1) {
			border-right: 1px solid #EAE9E8;
			
			&::before {
				right: 0;
			}
		}
	
		&:nth-child(2n + 2) {
			&::before {
				left: 0;
			}
		}
	
		&:nth-last-child(1) {
			&::after { 
				position: absolute; 
				top: 0; 
				left: 0;
				
				width: 1000rem; 
				height: 1px; 
		
				background-color: #EAE9E8;
				
				content: '';
			}
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;

		&::before {
			left: calc(var(--gl-indent) * -1);
		}
	}
}

.wish_list__card {
	position: relative;

	padding-top: 7.6rem;
	padding-bottom: 4rem;

	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	&:not(.wish_list__item:last-child &) {
		@include bp($point_2, min) {
			min-height: 29rem;
		}
		@include bp($point_3, $point_4) {
			min-height: 28.6rem;
		}
		@include bp($point_4 - 1) {
			min-height: 28.7rem;
		}
	}

	.wish_list__item:nth-child(2n + 1) & {
		@include bp($point_4, min) {
			padding-right: 1.8rem;
		}
	}

	.wish_list__item:nth-child(2n + 2) & {
		@include bp($point_2, min) {
			padding-left: 4rem;
		}
		@include bp($point_3, $point_4) {
			padding-left: 2rem;
		}
	}

	.wish_list__item:last-child & {
		@include bp($point_2, min) {
			min-height: 34.6rem;
		}
	}
}

.wish_list__card_img_w {
	position: relative;
	display: block;
	
	@include bp($point_2, min) {
		width: 14rem;
		margin-right: 2.4rem;
	}
	@include bp($point_3) {
		width: 13.5rem;
		margin-right: 2rem;
	}
	
	&::before {
		content: "";
		display: block;

		@include bp($point_2, min) {
			padding-top: 121.4%;
		}
		@include bp($point_3) {
			padding-top: 122.2%;
		}
	}
}

.wish_list__card_img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.wish_list__card_info {
	font-family: $font_1;

	@include bp($point_2, min) {
		margin-top: 1.6rem;
	}
	@include bp($point_3, $point_4) {
		margin-top: 1.6rem;
	}
	@include bp($point_4 - 1) {
		flex-grow: 1;
		margin-top: 1.6rem;
	}

	&.wish_list__card_info--width_mod {
		@include bp($point_5 - 1) {
			min-width: 21rem;
		}
	}
}

.wish_list__card_info_title {
	font-size: 1.8rem;
	font-weight: 500;
	text-transform: uppercase;
	color: $green_2;

	@include bp($point_2, min) {
		margin-bottom: .2rem;
	}
	@include bp($point_3, $point_4) {
		margin-bottom: .2rem;
	}
}

.wish_list__card_info_subtitle {
	font-size: 1.6rem;
	font-weight: 300;

	@include bp($point_2, min) {
		margin-bottom: .8rem;
	}
	@include bp($point_3, $point_4) {
		margin-bottom: .8rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 1rem;
	}
}

.wish_list__card_info_price {
	@extend %global_flex_block_row_wrap_flex-start;

	font-family: $font_3;
	font-size: 1.4rem;
	letter-spacing: .04em;
	
	@include bp($point_2, min) {
		margin-bottom: 1.4rem;
	}
	@include bp($point_3, $point_4) {
		margin-bottom: 1.4rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 1.4rem;
	}
}

.wish_list__card_info_old_price {
	position: relative;
	margin-right: .8rem;
	font-weight: 500;
	
	color: $gray_4;

	&::before {
		position: absolute;
		top: 0.8rem;
		left: -0.4rem;

		width: 109%;
		height: 2px;
		
		content: "";
		
		background-color: $gray_4;
	}
}

.wish_list__card_info_current_price {
	font-weight: 700;
	color: $red;
}

.wish_list__button {
	@extend %transparent_btn_1;
	@extend %btn_size_1;

	font-size: 1.4rem;
	font-weight: 500;
	font-family: $font_1;

	cursor: pointer;

	@include bp($point_2, min) {
		min-width: 20rem;
	}
	@include bp($point_3, $point_4) {
		min-width: 18.8rem;
	}
	@include bp($point_4 - 1) {
		min-width: 100%
	}
}

.wish_list__close {
	@extend %transition_color;

	position: absolute;

	width: 2rem;
	height: 2rem;

	color: $black;
	
	cursor: pointer;

	&:hover {
		color: $red;
	}

	&:not(.wish_list__close--pos_mod) {
		top: 4.7rem;
	}

	&.wish_list__close--pos_mod {
		top: 0;
		right: 0;
	}

	.wish_list__item:nth-child(2n + 1) & {
		@include bp($point_2, min) {
			right: 4rem;
		}
		@include bp($point_3, $point_4) {
			right: 2rem;
		}
		@include bp($point_4 - 1) {
			right: 0;
		}
	}

	.wish_list__item:nth-child(2n + 2) & {
		right: 0;
	}
}