.hero_v3 {
	@extend %banner_bg;

	min-height: calc(var(--full-screen) - var(--height-header));

	padding-top: 0.8rem;
}

.hero_v3__in {
	max-width: 134rem;
}

.hero_v3__bg {
	@extend %stretch;
	@extend %cover_img;
}

.hero_v3__menu {
	position: relative;
	z-index: 3;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 21.5rem;
		}
	}

	@include bp($point_3, $point_5) {
		&:not(:last-child) {
			margin-bottom: 14.6rem;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 14.3rem;
		}
	}
}

.hero_v3__list {
	@extend %global_flex_block_row_wrap_flex-start_center;

	margin: 0 -0.8rem -1rem 0;
}

.hero_v3__item {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	padding: 0 0.8rem 1rem 0;

	&:not(:last-child) {
		&::after {
			content: "";
			width: 3px;
			height: 3px;
			border-radius: 50%;
			margin-left: 1rem;

			background-color: $green_2;
		}
	}
}

.hero_v3__link {
	@extend %transition_color;

	font-weight: 400;
	font-size: 1rem;
	line-height: 1.4;
	color: $green_2;

	&.hero_v3__link--active_mod {
		color: $gray_3;
	}

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray;
	}
}

.hero_v3__content {
	@extend %global_flex_block_column_nowrap_center_center;

	position: relative;
	z-index: 3;

	max-width: 63rem;
	margin: 0 auto;
}

.hero_v3__subtitle,
.hero_v3__title {
	color: $gray_2;
	text-align: center;
	font-family: $font_2;
}

.hero_v3__subtitle {
	@extend %h4;

	@include bp($point_5, min) {
		&:not(:last-child) {
			margin-bottom: 5px;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 1px;
		}
	}
}

.hero_v3__title {
	@extend %h2;

	@include bp($point_5, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.hero_v3__text {
	@extend %text;

	text-align: center;
	letter-spacing: 0.03em;
	color: $white;
}
