//---------------------------------------------fonts
$font_path: "../fonts/";
$font_1: "Avenir", sans-serif;
$font_2: "Cormorant", sans-serif;
$font_3: "Jost", sans-serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;

$gray: #cec6c4;
$gray_2: #eae9e8;
$gray_3: #b7c1c5;
$gray_4: #cecece;
$orange: #e64926;
$green: #254a5a;
$green_2: #0f303f;
$red: #fd5454;

$color_text_base: $green_2;
$color_link_base: $green_2;
$body_bg: $white;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup: 101,
	header: 100,
	footer: 100,
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 133.2rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0.5rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_0: 1441px;
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 2.8rem;
$input_offset: 0 2rem;
$input_font_size: $font_size_base;
$input_border_color: $green_2;
$input_border_color_active: $orange;
$input_placeholder_color: rgba($black, .4);
$input_text_color: $green_2;
$input_radius: $gl_radius;
//---------------------------------------------forms###
