// menu list
.header_list {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		margin: 0 max(-3.1vw, -4.5rem) -2rem 0;
	}

	@include bp($point_3) {
		flex-shrink: 0;
		margin: 0 -2px -2rem 0;
	}
}

.header_list__item {
	@include bp($point_2, min) {
		padding: 0 min(2.6vw, 4rem) 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 2px 2rem 0;
	}
}

.header_list__link {
	display: block;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: $green_2;

	&.header_list__link--invisible_mod {
		color: $gray_3;
	}

	&.header_list__link--search_mod {
		cursor: pointer;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-right: 1.8rem;
			}
	
			@include bp($point_3) {
				margin-right: 1rem;
			}
		}
	}

	&:not(.header_list__link--search_mod) {
		@extend %transition_color;
		@extend %active_border;

		&:hover{
			color: $black;
		}

		@include bp($point_2, min) {
			padding: 0 4px;
		}

		@include bp($point_3) {
			padding: 0 1.1rem;
		}
	}

	&.header_list__link--active_state {
		&::before{
			opacity: 1;
		}

		@include bp($point_3) {
			pointer-events: none;
		}
	}

	&:hover,
	&:focus{
		text-decoration: none;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.header_list__form {
	@include bp($point_3, $point_4) {
		padding-left: 4.8vw;

		margin-right: max(6.6vw, 1.8rem);
	}

	@include bp($point_4 - 1) {
		margin-right: 1.3rem;
		
		&.header_list__form--tablet_mod {
			display: none;
		}
	}
}

.header_list__input {
	@include flex_block(row, wrap, flex-start);
}

.header_list__field {
	@include bp($point_2, min) {
		max-width: 15rem;
		margin-top: -1.1rem;
	}

	@include bp($point_3) {
		margin-top: -1.3rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 21rem;
	}

	@include bp($point_4 - 1) {
		max-width: 20rem;
	}
}