@import "ScssHelpers/_helpers_all.scss";

.card_popup {
	@extend %popup;
}

.card_popup__in {
	@extend %global_flex_block_row_wrap_center_center;
	width: 100%;
}

.card_popup__close {
	position: absolute;
	top: 3rem;
	right: 3rem;

	cursor: pointer;
}

.card_popup__close_icon {
	width: 2rem;
	height: 2rem;
	color: $green_2;
}

.card_popup__content_wrap {
	width: 100%;
	position: relative;
	padding: 4.6rem 4rem 4.8rem;

	background: $white;
	transition: transform 0.5s;
	transform: scale(0.5);

	.popup--open & {
		transform: scale(1);
	}

	#popup_login & {
		width: 100%;

		@include bp($point_4, min) {
			max-width: 42.8rem;
			padding: 7.6rem 9rem 7.6rem;
		}

		@include bp($point_4 - 1) {
			padding: 5rem var(--gl-indent);
			@include flex_block(row, wrap, center, center, center);
		}
	}

	@include bp($point_4, min) {
		max-width: 52rem;
	}

	@include bp($point_4 - 1) {
		min-height: 100vh;
	}
}

.card_popup__content {
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.36;

	color: $green_2;

	h4 {
		font-weight: 700;
		text-transform: uppercase;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	p {
		opacity: 0.7;
		letter-spacing: 0.032em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	strong {
		display: block;
		font-weight: 700;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

//===============Login==============

.card_popup__logon_wrap {
	width: 100%;
}

.card_popup__title {
	font-weight: 300;
	letter-spacing: 0.03em;
	text-transform: uppercase;

	color: $green_2;
	text-align: center;

	@include bp($point_2, min) {
		margin-bottom: 3.2rem;
		font-size: 2.5rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 3.6rem;
		font-size: 2rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2.8rem;
		font-size: 2rem;
	}
}

.card_popup__form {
	width: 100%;
	margin-bottom: 3.2rem;
}

.card_popup_form__block {
	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.card_popup_form__block_password {
	position: relative;
	margin-bottom: 2rem;
}

.card_popup_form__input {
	width: 100%;
	padding: 0.8rem 0 1rem;

	font-size: 1.4rem;
	font-family: $font_1;

	color: $green_2;

	border: none;
	border-bottom: 1px solid $green_2;

	&.card_popup_form__input--password_mod {
		padding-right: 3rem;
	}
}

.card_popup__button_show_pass {
	z-index: 1;
	position: absolute;
	top: 50%;
	right: 0;
	width: 1.4rem;
	height: 1.4rem;
	transform: translateY(-50%);

	cursor: pointer;
}

.card_popup__button {
	@extend %btn_size_1;
	@extend %dark_btn_1;
	width: 100%;
	text-align: center;
	cursor: pointer;
}

.card_popup__logon_list {
	@include flex_block(row, wrap, center, center, center);
	margin: 0 -1.2rem -2rem 0;
}

.card_popup__logon_item {
	padding: 0 1.2rem 2rem 0;
}

.card_popup__logon_link {
	position: relative;
	font-size: 1.2rem;
	font-weight: 300;
	letter-spacing: 0.04em;
	color: $orange;

	&:before {
		@extend %transition_opacity;
		position: absolute;
		bottom: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 1px;
		background-color: $orange;
		opacity: 0;
		content: "";
	}

	&:focus,
	&:hover {
		text-decoration: none;

		&:before {
			opacity: 1;
		}
	}

	.card_popup__logon_item:not(:last-child) & {
		&:after {
			position: absolute;
			top: 2px;
			font-size: inherit;
			right: -0.8rem;

			color: $orange;
			content: "/";
		}
	}
}
