%form_el_label {
	display: block;
	margin-bottom: 0.4rem;

	font-size: 1.6rem;
	color: $black;
}

%form_el_default {
	display: block;
	width: 100%;
	height: $input_height;
	padding: $input_offset;
	font-size: $input_font_size;
	font-family: $font_1;
	color: $input_text_color;
	box-shadow: inset 0 -1px 0 0 $input_border_color;
	border: none;
	background-clip: padding-box;

	transition: box-shadow 0.3s ease;

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

	&:hover,
	&:focus {
		box-shadow: inset 0 -1px 0 0 $input_border_color_active;
	}
}

%form_element_card {
	@extend %transition_all;

	width: 100%;
	height: 3.5rem;
	padding: 5px 5px 5px 0;

	font-size: 1.4rem;
	line-height: 1.7;
	letter-spacing: 0.03em;
	font-family: $font_1;
	color: $green;

	border: none;
	box-shadow: inset 0 -1px 0 0 $input_border_color;
	background-clip: padding-box;
	appearance: none;

	&::-webkit-input-placeholder {
		color: $green;
	}

	&::-moz-placeholder {
		color: $green;
	}

	&:-ms-input-placeholder {
		color: $green;
	}

	&:hover,
	&:focus {
		box-shadow: inset 0 -1px 0 0 $input_border_color_active;
	}
}

%input_element_1 {
	@extend %transition_all;

	font-size: 1.6rem;
	font-family: $font_1;
	text-align: center;

	color: $green_2;

	border: none;
	box-shadow: inset 0 -1px 0 0 $input_border_color;
	background-clip: padding-box;

	&:hover,
	&:focus {
		box-shadow: inset 0 -1px 0 0 $input_border_color_active;
	}
}

%input_element_2 {
	@extend %transition_all;

	font-size: 1.4rem;
	letter-spacing: 0.035em;

	color: $green_2;

	border: none;
	box-shadow: 0 1px 0 $green;
	background-clip: padding-box;

	&:hover,
	&:focus {
		box-shadow: 0 1px 0 $input_border_color_active;
	}
}
