.hero_v2 {
	@extend %banner_bg;

	min-height: calc(var(--full-screen) - var(--height-header));

	padding-top: 0.8rem;
}

.hero_v2__in {
	max-width: 134rem;
}

.hero_v2__bg {
	@extend %stretch;
	@extend %cover_img;
}

.hero_v2__menu {
	position: relative;
	z-index: 3;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 22.3rem;
		}
	}

	@include bp($point_3, $point_5) {
		&:not(:last-child) {
			margin-bottom: 16.2rem;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 16.7rem;
		}
	}
}

.hero_v2__content {
	@extend %global_flex_block_column_nowrap_center_center;

	position: relative;
	z-index: 3;

	max-width: 63rem;
	margin: 0 auto;
}

.hero_v2__title {
	@extend %h2;

	text-align: center;
	color: $gray_2;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1.7rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 3rem !important;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_5 - 1) {
		font-size: 3.2rem;
	}
}

.hero_v2__text {
	@extend %text;

	text-align: center;
	letter-spacing: 0.03em;
	color: $white;
}
