.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;
	
	border-top: 1px solid $gray_2;
	will-change: transform;

	@include bp($point_2, min) {
		padding: 6.1rem var(--gl-indent) 1.5rem;
	}

	@include bp($point_3) {
		padding: 5.7rem var(--gl-indent) 1.5rem;
	}
}

.footer__in {
	max-width: $page_width;
	margin: 0 auto;
}

.footer__title {
	@extend %h3;

	text-align: center;

	color: $green_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.9rem;
		}
		@include bp($point_3, $point_6) {
			margin-bottom: 2rem;
		}

		@include bp($point_6 - 1) {
			margin-bottom: 2.3rem;
		}
	}
}

.footer_form {
	&:not(:last-child) {
		@include bp($point_6, min) {
			margin-bottom: 3.1rem;
		}

		@include bp($point_6 - 1) {
			margin-bottom: 3.2rem;
		}
	}

	@include flex_block(row, nowrap, center, flex-end);
}

.footer_form__field {
	width: 100%;
}

.footer_form__input_w {
	max-width: 25rem;
	width: 100%;

	&:not(:last-child) {
		margin-right: 1.2rem;
	}
}

.footer_form__input {
	width: 100%;
	padding: 0.7rem;
}

.footer_form__btn_submit {
	@extend %transition_opacity;

	width: 1.8rem;
	height: 1.8rem;
	padding: 0.5rem 0.4rem;

	border: 1px solid $orange;
	color: $orange;
	cursor: pointer;

	&:hover {
		opacity: 0.5;
	}

	@include bp($point_4) {
		display: none;
	}
}

.footer_nav {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6.1rem;
		}
	}

	@include bp($point_3, $point_6) {
		&:not(:last-child) {
			margin-bottom: 5.1rem;
		}
	}

	@include bp($point_6 - 1) {
		&:not(:last-child) {
			margin-bottom: 6.2rem;
		}
	}
}

.footer_nav__list {
	@include bp($point_6, min) {
		margin: 0 -4rem -2rem 0;
	}

	@include bp($point_6 - 1) {
		margin: 0 -1rem -2rem 0;
	}

	@include flex_block(row, wrap, center, flex-start);
}

.footer_nav__item {
	@include bp($point_6, min) {
		padding: 0 4rem 2rem 0;
	}

	@include bp($point_6 - 1) {
		padding: 0 1rem 2rem 0;
	}

	@include bp($point_6, min) {
		font-size: 1.6rem;
	}

	@include bp($point_6 - 1, $point_7) {
		font-size: 1.4rem;
	}

	@include bp($point_7 - 1) {
		font-size: 1.3rem;
	}
}

.footer_nav__link {
	@extend %transition_color;

	font-weight: 400;
	letter-spacing: 0.035em;

	color: $gray_3;

	&:hover {
		text-decoration: none;
		color: darken($gray_3, 40);
	}
}

.footer_bottom {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		height: 1px;
		width: 100vw;
		transform: translateX(-50%);
		background-color: $gray_2;
	}

	@include flex_block(row, wrap, space-between, center, center);

	@include bp($point_6, min) {
		padding-top: 1.5rem;
	}

	@include bp($point_6 - 1) {
		padding-top: 1.8rem;
	}
}

.footer_bottom__item {
	@include bp($point_4) {
		letter-spacing: 0.02em;
	}

	span {
		&:first-child {
			@include bp($point_4, min) {
				margin-right: 0.7rem;
			}

			@include bp($point_4 - 1, $point_6) {
				margin-right: 0.9rem;
			}

			@include bp($point_6 - 1) {
				margin-right: 0.6rem;
			}
		}
	}

	@include bp($point_4, min) {
		font-size: 1.2rem;
	}

	@include bp($point_4 - 1, $point_6) {
		font-size: 1.1rem;
	}

	@include bp($point_6 - 1) {
		font-size: 0.8rem;
	}
}
